import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { ApiservicesService } from '../../provider/apiservices.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { DataTransferService } from '../../services/data-transfer.service';

@Component({
  selector: 'app-say-cheers-board',
  templateUrl: './say-cheers-board.page.html',
  styleUrls: ['./say-cheers-board.page.scss'],
})

export class SayCheersBoardPage implements OnInit{

  sayCheersBoard:any;
  segmentSectionDiv:string="cheer";
  pageTitle:any;
  value:number=0;
  errorMessage:string="";
  myInfiniteScroll:any;
  myinfiniteScrollCall:boolean=false;
  cheerBoard:any;
  Settings:any;
  leaderTitle:any;
  countData:any;
  segmentModel:any='cheer';
  showErrorImage:any="";
  no_data_message:any="";

  constructor(private router:Router,private storageService:LocalStorageService,private apiservices:ApiservicesService,
    private zone:NgZone,private sendData:DataTransferService) {

  }









  ngOnInit(){
    // this.storageService.getStorage().then(res=>{
    //   this.sayCheersHome();
    //   console.log("ionic lifecycle called getStorage==>",res);
    // });
  }

  ionViewDidEnter(){
    if(this.sayCheersBoard){
      this.zone.run(()=>{
        this.segmentModel='cheer';
      });
    }
    console.log("ionViewDidEnter");
    this.storageService.getStorage().then(res=>{
      setTimeout(()=>{
        this.countData=undefined;
        this.sayCheersBoard=undefined;
        this.cheerBoard=undefined;
        this.value=0;
        this.sayCheersHome();
        console.log("ionic lifecycle called getStorage==>",res);
      },500)
    });
  }

  // public ionViewWillEnter(): void{
  //   this.eventService.getObservable().subscribe((data) => {
  //     console.log("publishData Data received==", data);
  //     if(data.data.commingFrom=="reloadHomeApi" && data.data.reloadHomePage==true){
  //       console.log("publishData Data forceupdate==", data);
  //       this.allData=undefined;
  //       this.sayCheersHome();
  //     }
  //   });
  // }
  
  goToBadgeList(totalCount, value){
    this.zone.run(()=>{
      this.sendData.sayCheers=value;
      this.router.navigate(['/say-cheers-badges']);
      // if(totalCount>0){
      //   this.sendData.sayCheers=value;
      //   this.router.navigate(['/say-cheers-badges']);    
      // }else{}
    });
  }

  segmentChanged(ev: any,tabsData){
    this.zone.run(()=>{
      this.showErrorImage="";
      this.no_data_message="";
      this.cheerBoard=undefined;
      this.errorMessage="";
      this.value=0;
      console.log('Segment change000000000000', ev);
      console.log('Segment changed2', tabsData);
      console.log('Segment changed1', ev.detail.value);
      this.zone.run(()=>{
        this.segmentSectionDiv=ev.detail.value;
        if(this.segmentSectionDiv=='leader'){
          console.log('Segment changed100', ev.detail.value);
          this.leaderCheerData(tabsData.leaderboard.requestTab);
        }
        else  if(this.segmentSectionDiv=='cheer'){
          this.cheerBoard=undefined;
          console.log('Segment changed1000', ev.detail.value);
          this.leaderCheerData(tabsData.cheerboard.requestTab);
        } 
        else{
          this.cheerBoard=undefined;
          console.log('Segment changed1000', ev.detail.value);
          this.leaderCheerData(tabsData.cheerboard.requestTab);
        }
      });
    });
  }

  goToNext(){
    this.zone.run(()=>{
      this.router.navigate(["/say-cheers-list"]);
    });
  }

  sayCheersHome(){
    this.zone.run(()=>{
      console.log("noticeBoard Detail api call function");
      let apikey={}
      this.apiservices.apiCallWithLoginToken(URLS.sayCheersHomeApi, apikey).subscribe((result)=>{
        console.log("noticeBoard Detail api result==", result);
        this.pageTitle=result.data.settings.page_header;
        if(result.status==1 || result.status=='1'){
          this.countData=result.data;
          this.sayCheersBoard=result.data.settings;
          this.leaderCheerData(this.sayCheersBoard.cheerboard.requestTab);
        }else{
          this.apiservices.showToastMessage(result.message,'top',3000,'redBg');
        }
      }, err =>{
        this.apiservices.showToastMessage(JSON.stringify(err),'top',3000,'redBg');
      })
    });
  }

  goToMessage( index){
    this.zone.run(()=>{
      let obj={
        allData:this.cheerBoard,
        index:index,
        pageHeader:this.Settings,
      }
      this.sendData.sayCheers=obj;
      // if(this.segmentSectionDiv=='cheer'){
      //   this.sendData.myParam=this.sayCheersBoard.cheerboard.requestTab;
      // }else{
      //   this.sendData.myParam=this.sayCheersBoard.leaderboard.requestTab;
      // }
      this.router.navigate(["/say-cheers-slide-detail"]);
    });
  }

  leaderCheerData(tabsData){
    this.zone.run(()=>{
      console.log("noticeBoard List api call function");
      let apikey={
        request_tab:tabsData,
        limit:this.value,
      }
      this.apiservices.apiCallWithLoginToken(URLS.leaderCheerApiUrl, apikey).subscribe((result)=>{
        console.log("noticeBoard List api result==", result);
        this.Settings=result.data.settings;
        if(result.status==1 || result.status=='1'){
          this.leaderTitle=result.data.boardlist.list_header;
          if(this.cheerBoard==undefined){
            this.cheerBoard=result.data.boardlist.board_list;
            console.log("HrPolicy List Infinite Scroll if1==", this.cheerBoard);
          }else{
            this.cheerBoard=this.cheerBoard.concat(result.data.boardlist.board_list);
            console.log("HrPolicy List Infinite Scroll else1==", this.cheerBoard);
          }
          if(this.myinfiniteScrollCall==true){
            console.log("HrPolicy List Infinite Scroll if2==", this.cheerBoard);
            this.myInfiniteScroll.target.complete();
            this.errorMessage = result.message;
            this.myinfiniteScrollCall=false;
          }
        }else{
          this.errorMessage = result.message;

          this.no_data_message=result.data.settings.no_data_msg;
          this.showErrorImage=result.data.settings.no_data_image;

          console.log("HrPolicy List Infinite Scroll12==", this.cheerBoard);
          if(this.myinfiniteScrollCall==true){
            console.log("HrPolicy List Infinite Scroll123==", this.cheerBoard);
            this.myInfiniteScroll.target.complete();
            this.myinfiniteScrollCall=false;
          }
        }
      }, err =>{
        this.apiservices.showToastMessage(JSON.stringify(err),'top',3000,'redBg');
      })
    });
  }

  doInfinite(infiniteScroll){
    this.myinfiniteScrollCall=true;
    console.log("Begin async operation 1==");
    setTimeout(() => {
      this.value=this.cheerBoard.length;
      console.log("Begin async operation 2==",this.value);
      this.myInfiniteScroll = infiniteScroll;
      if(this.segmentSectionDiv=='cheer'){
        this.leaderCheerData(this.sayCheersBoard.cheerboard.requestTab);
      }else{
        this.leaderCheerData(this.sayCheersBoard.leaderboard.requestTab);
      }      
    }, 500);
  }
  
  






  
}